<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="12">
          <v-img
              :src="require('../../assets/logo-white.png')"
              contain
              height="80"
          />
        </v-col>

        <v-col cols="12" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title text-center">
            Estado de tus tramites
          </h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12">Documentos pendientes</v-col>
      </v-row>

      <v-row class="text-center">
        <v-col>
          <div class="mb-2" style="color: #084068">{{ operation.document_name }}</div>

          <div class="call-to-action">
            <v-row style="background-color: #184C72; color: white">
              <v-col class="text-left">Estado: {{ operation.status.name }}</v-col>
              <v-col class="text-right">{{ operation.created_at | dateTime }}</v-col>
            </v-row>

            <v-row>
              <v-col class="text-center">
                <div>Costo del documento : S/. {{ operation.price }}</div>
                <div>Código de solicitud: {{ operationId | padleft }}</div>
                <div>Abogado responsable: LegalHelp</div>
              </v-col>
            </v-row>
          </div>
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="12">
          <div class="call-to-action-white" style="margin: 1rem; position: relative">
            <div class="text">
              Para completar tu solicitud envianos el comprobante del depósito. (Imagen JPG,
              PNG O PDF. Máximo 5 Mb.)
            </div>
            <img :src="require('../../assets/icons/lawyer-07.svg')" style="position: absolute; right: 0; bottom: 0">
          </div>
        </v-col>
        <v-col v-if="operation.hasVoucher" cols="12">
          <v-btn color="secondary" block :href="operation.voucherUrl" target="_blank">Comprobante actual</v-btn>
        </v-col>
        <v-col cols="12">
          <v-file-input placeholder="seleccionar comprobante..." v-model="file" @change="onAttach"></v-file-input>
        </v-col>
        <v-col cols="12">
          <v-btn @click="onSubmit" :loading="isLoading" color="primary" block>Enviar</v-btn>
        </v-col>
      </v-row>

      <button-back @click="goToIndex"></button-back>

    </v-container>
  </div>
</template>

<script>
import {mapGetters} from "vuex"
import axios from "axios"
import ButtonBack from "../../components/ButtonBack";

export default {
  name: 'OperationPayment',
  inject: ['notyf'],
  components: {ButtonBack},
  data: () => ({
    isLoading: false,
    operation: {
      status: {name: 'Loading...'},
      hasVoucher: false,
    },
    file: '',
  }),
  computed: {
    ...mapGetters(['user']),
    operationId() {
      return this.$route.params.id
    }
  },
  created() {
    this.loadOperation()
  },
  methods: {
    loadOperation() {
      let url = 'operations/' + this.operationId
      axios.get(url).then((response) => {
        this.operation = response.data
      })
    },
    goToIndex() {
      this.$router.push({name: 'OperationIndex'})
    },
    onAttach(file) {
      this.file = file
    },
    onSubmit() {
      this.isLoading = true

      let formData = new FormData();
      formData.append('_method', 'PUT');
      formData.append('voucher', this.file);

      axios.post('operations/' + this.operationId, formData, {headers: {'Content-Type': 'multipart/form-data'}}
      ).then((response) => {
        this.operation = response.data
        this.notyf.success('Comprobante cargado correctamente.');
        this.$router.push({name: 'OperationsPendingConfirm', params: {id: this.operationId}})
      }).catch(() => {
        this.notyf.error('Ups! Ocurrio un error, por favor intentelo mas tarde.');
      }).then(() => {
        this.isLoading = false
      });
    }
  }
}
</script>
